import React from 'react';
import Row from './Row';
import { Droppable } from 'react-beautiful-dnd';
import './index.css';

const Section = ( props ) => {
	const rowsArray = props.sectionRows;
	const rows = rowsArray.map( ( rowId, index ) => {
		return <Row rowColumns={props.allRows[ rowId ].cols} sectionName={props.sectionName} key={rowId} rowId={rowId}
			allColumns={props.allColumns} allFields={props.allFields} viewType={props.viewType}
			index={index} addFieldModal={props.addFieldModal} fieldProps={props.fieldProps} addRow={props.addRow} openModal={props.openModal} deleteRow={props.deleteRow} />
	} );

	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	let upsellLink = '';

	if ( view_config.prefix === 'nf' ) {
		upsellLink = 'https://nfviews.com/pricing/?utm_source=view-edit-page&utm_medium=nfviews-advance-settings&utm_campaign=nf-views-lite-version'
	} else if ( view_config.prefix === 'cf7' ) {
		upsellLink = 'https://cf7views.com/pricing/?utm_source=view-edit-page&utm_medium=cf7views-advance-settings&utm_campaign=cf7-views-lite-version'
	} else {
		upsellLink = 'https://formviewswp.com/pricing/?utm_source=view-edit-page&utm_medium=wpformsviews-advance-settings&utm_campaign=wpforms-views-lite-version'
	}

	return (
		<div className="viewSectionContainer viewTabChild" >
			<h2 className="viewSecHeading"> {props.sectionLabel} </h2>
			<Droppable type={`section-${props.sectionName}`} droppableId={props.sectionName}>
				{( provided, snapshot ) => {
					const droppableClasses = snapshot.isDraggingOver ? 'activeRow' : '';
					return (
						<div className={droppableClasses + ' droppableRow'}
							{...provided.droppableProps} ref={provided.innerRef} >
							{rows}
						</div>
					)
				}}
			</Droppable>
			{props.sectionName == 'singleloop' && <a href={upsellLink} target="_blank"><i className="fas fa-info-circle"></i> &nbsp;Available in Pro Version!</a>}
			{( ( ( props.viewType == 'list' && props.sectionName == 'loop' ) || props.sectionName != 'loop' ) && props.sectionName !== 'singleloop' ) &&
				<span className="addRow" onClick={( event ) => props.addRow( event, props.sectionName )} >
					<span className="addRowIcon"><i className="fas fa-plus"></i></span> <span className="addRowText">Add Row</span>
				</span>
			}
		</div>
	);

};

export default Section;