import React, { useState }  from 'react';
import './index.css';

const FieldSettings = ( props ) => {
	const modalProps = props.modalProps;
  const [useCustomLabel, setuseCustomLabel] = useState(modalProps.fieldSettings.useCustomLabel);

	const fieldId = modalProps.currentId;
	let fieldSettings = modalProps.fieldSettings;
	const updateSettings = (e) =>{
		const settingsName = e.target.className;
		fieldSettings[settingsName] =  e.target.type === 'checkbox' ?  e.target.checked :  e.target.value;
		 if( settingsName === 'useCustomLabel'){
		  setuseCustomLabel(e.target.checked);
		 }

	}

	return (
		<div className="fieldSettings pure-form pure-form-aligned">
		<fieldset>
			<div className="pure-control-group">
				<label>  Use Custom Label?</label>
				<input className="useCustomLabel"  defaultChecked={fieldSettings.useCustomLabel} onChange={updateSettings}  type="checkbox" />
			</div>
			{useCustomLabel && <div className="pure-control-group">
				<label> Custom Label</label>
				<input className="label" defaultValue={fieldSettings.label} onChange={updateSettings} type="text" />
			</div>}
			<div className="pure-control-group">
				<label> Css Class</label>
				<input className="customClass" defaultValue={fieldSettings.customClass} onChange={updateSettings} type="text" />
			</div>
			<div className="pure-control-group form-action-btns">
				<button className="pure-button pure-button-primary" onClick={ ( event ) => modalProps.saveFieldSettings( event, fieldId, fieldSettings )} >Save</button>
				<button className="pure-button" onClick={modalProps.closeModal} >Cancel</button>
			</div>
			</fieldset>
		</div>
	)

}

export default FieldSettings;