import React, { Fragment } from 'react';
import Column from './Column';
import { Draggable } from 'react-beautiful-dnd';
import './index.css';


const Row = ( props ) => {
	const columnsArray = props.rowColumns;
	const sectionName = props.sectionName;
	const rowId = props.rowId;
	const columns = columnsArray.map( ( colId, index ) => {
		return <Column title={props.allColumns[ colId ].title} sectionName={props.sectionName} columnFields={props.allColumns[ colId ].fields} key={index}
			openModal={props.openModal} allFields={props.allFields} fieldProps={props.fieldProps} colId={colId} size={props.allColumns[ colId ].size} />
	} );

	return (
	<Fragment>
			<Draggable draggableId={rowId} index={ props.index } type={`section-${props.sectionName}`}>
				{( provided ) => {
					return (
						<div ref={provided.innerRef} { ...provided.draggableProps }   className="row pure-g">

							{
							( props.sectionName !='loop' ) && <div className="rowSettingsPanel">
									<span>
										<span title="Drag to reorder"  className="row-layout-icon view-setting-icon" { ...provided.dragHandleProps }> <i className="fas fa-arrows-alt"></i></span>
										<span title="Select Layout" className="view-layout row-layout-icon view-setting-icon" onClick={( event ) => props.openModal( event, 'layout', 'Choose Layout', rowId, props.sectionName )}><i className="fas fa-columns"></i></span>
										<span className="view-row-remove view-setting-icon" onClick={( event ) => props.deleteRow( event, rowId, sectionName )}><i className="fas fa-trash-alt"></i></span>
									</span>
								</div>
							}
							 { ((props.viewType == 'table' || props.viewType == 'datatable') && props.sectionName =='loop') && <div className="hiddendrag" { ...provided.dragHandleProps }>

							</div>

							}
							<div className="columnPanel">
								{columns}
							</div>
							{provided.placeholder}
						</div>
					)
				}

				}
			</Draggable>
			{ props.viewType == 'table' && props.sectionName =='loop' && <p>Each field will be displayed as a column in the table.</p>}
		</Fragment>
	);
}

export default Row;
